<template>
  <div id="shell">
    <div id="header">
      <div id="terminal">
        
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
#shell {
// 
}
</style>
<script>
// vue
// terminalsocket

import { mapState } from 'vuex'
import 'xterm/css/xterm.css'
import { Terminal } from 'xterm'
import { FitAddon } from 'xterm-addon-fit'
import { attach } from 'xterm-addon-attach'
import { search } from 'xterm-addon-search'
let terminal = null
export default {
  data() {
    return {}
  },
  computed: {
    ...mapState('login', {
      socket: 'socket',
      isLogined: 'isLogined',
    }),
  },
  created() {
    // 
  },
  destroyed() {
    // commandRes
    this.socket.removeAllListeners(['commandRes'])
    terminal.dispose()
    // this.socket.close()
  },
  mounted() {
    terminal = new Terminal()
    const fitAddon = new FitAddon()
    terminal.loadAddon(fitAddon)
    // FIXME shell
    fitAddon.fit()
    const ele = document.getElementById('terminal')
    terminal.open(ele)
    terminal.focus()
    // socket
    this.socket.on('commandRes', (commandRes) => {
      terminal.write(commandRes)
      // TODO exitcommandReslogoutshell app
      // TAG shell
    })
    let printable = false
    // 
    let command = ''
    // terminal.prompt = () => {
    //   terminal.write('\n\r# ')
    // }
    // term
    terminal.onKey((data) => {
      const key = data.domEvent.key
      // 
      printable =
        !data.domEvent.altKey &&
        !data.domEvent.ctrlKey &&
        !data.domEvent.metaKey

      switch (key) {
        case 'Enter':
          // terminalcommand
          for (let i = command.length; i > 0; i--) {
            terminal.write('\b \b')
          }
          this.uploadCommand(command)
          command = ''
          //   terminal.prompt();
          break
        case 'Backspace':
          {
            // 
            command = command.slice(0, -1)
            terminal.write('\b \b')
          }
          break
        default:
          if (printable) {
            terminal.x++
            terminal.write(key)
            command += key
          }
      }
    })
  },
  methods: {
    uploadCommand: function (command) {
      // 
      this.socket.emit('uploadCommand', command)
    },
  },
}
</script>
