import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '@/platform/apps/Login/Index.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Desktop',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(
        /* webpackChunkName: "about" */ '@/platform/apps/Desktop/Index.vue'
      ),
    meta: {
      keepAlive: true,
    },
    beforeEnter: (to: any, from: any, next: any) => {
      // cookiesessionlocalStorage
      // console.log(this)

      next()
    },
    children: [
      {
        path: '/shell',
        component: () => import('@/platform/apps/Shell/Index.vue'),
        meta: {
          keepAlive: true,
          requireAuth: true
        },
      },
      {
        path: '/login',
        name: 'login',
        component: () => import('@/platform/apps/Login/Index.vue'),
        meta: {
          keepAlive: true,
          requireAuth: false
        },
      },
      {
        path: '/fileManager',
        component: () => import('@/platform/apps/FileManager/Index.vue'),
        meta: {
          keepAlive: true
        },
      },
      {
        path: '/notfound',
        component: () => import('@/platform/apps/NotFound/Index.vue'),
        meta: {
          keepAlive: true
        },
      }
    ],
  },
  // {
  //   path: '/login',
  //   name: 'Login',
  //   component: Login,
  //   meta: {
  //     keepAlive: true,
  //   },
  // },

  // {
  //   path: '/*',
  //   name: 'notfound',
  //   component: () => import('@/platform/apps/NotFound/Index.vue'),
  //   meta: {
  //     keepAlive: true,
  //   },
  // },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})
router.beforeEach((to: any, from: any, next: any) => {
  // TODO:LoginisLogined
  // if(to.path === '/login' && )
  // 
  // localStorage
  if(to.meta.requireAuth == true && !sessionStorage.getItem('isLogined')) {
    alert('')
    next('/login')
  }
  // 
  // if(to.path === '/login' && sessionStorage.getItem('isLogined')) {
  //   alert('')
  //   next('/')
  // }
  // 
  next()
})

export default router
